import axios from 'axios'
import moment from 'moment'
import Sobel from 'sobel'
import { Observable } from 'rxjs'
import TestEngine from './testEngine'
import { BigNumber } from 'bignumber.js'
import { TYPES, covertMessage } from './message'
import plugin, { pluginName } from '../../libraries/babel-plugin-convert-call'

const babel = require('@babel/standalone')
const sanboxEngine = require('../../libraries/sandbox-core-engine')
const chai = require('chai')
const expect = chai.expect
const should = chai.should()
const assert = chai.assert

const DEFAULT_PRESETS = [
  'es2017',
  ['stage-0', { legacy: false, decoratorsBeforeExport: true }]
]
const DEFAULT_PLUGINS = [
  'transform-spread',
  'transform-classes',
  'transform-block-scoping',
  'proposal-function-bind',
  'transform-arrow-functions',
  'proposal-class-properties',
  'transform-async-to-generator',
  'proposal-object-rest-spread',
  'proposal-export-default-from',
  'transform-block-scoped-functions',
  'proposal-async-generator-functions',
  pluginName
]
// Javascript sanbox required for exec the javascript code without threatened main application
export default class Sandbox {
  constructor (global, variables, presets = undefined, plugins = undefined) {
    this.presets = presets || DEFAULT_PRESETS
    this.plugins = plugins || DEFAULT_PLUGINS
    this.variables = variables

    this.responseObserver = null
    this.responseSubscription = null
    // Make a generator function for response result
    this.responseObservable = new Observable((observer) => {
      this.responseObserver = observer
    })
    this.testEngine = new TestEngine()
    this.global = this.prepareGlobalObjects(global || {})
    babel.registerPlugin(pluginName, plugin)
  }
  prepareGlobalObjects (inputs) {
    return {
      // Type
      String,
      Object,
      Array,
      Number,
      Date,
      Error,
      // Global function
      JSON,
      Symbol,
      Promise,
      Math,
      RegExp,
      ...this.customGlobalObjects(),
      ...inputs
    }
  }
  exec (source, callbacks = {}) {
    try {
      source = `
${source}
      `
      const result = babel.transform(source, {
        presets: this.presets,
        plugins: this.plugins
      }).code
      if (this.responseSubscription) {
        this.responseSubscription.unsubscribe()
      }
      this.responseSubscription = this.responseObservable.subscribe({
        next: callbacks.onMessage || undefined,
        error: callbacks.onError || undefined,
        complete: this.processComplete
      })
      const code = sanboxEngine.compileSanbox(result)
      const resultExcuse = code(
        { ...this.global, window: this.global },
        this.variables || {}
      )
      if (resultExcuse) {
        this.sendOutput(TYPES.execuse_result, resultExcuse)
      }
    } catch (err) {
      console.log('%c---------COMPILER-ERROR---------', 'background-image: linear-gradient(-90deg, #ff9a9e, #fad0c4); color: #FFFFFF')
      console.log(err)
      console.log('%c--------------------------------', 'background-image: linear-gradient(-90deg, #ff9a9e, #fad0c4); color: #FFFFFF')
      return this.createOutput(TYPES.execuse_error, err.message)
    }
  }
  createOutput (type, message, params = {}) {
    return {
      type,
      message: covertMessage(type, message),
      ...params
    }
  }
  sendOutput (type, message, params = {}) {
    this.responseObserver && this.responseObserver.next(this.createOutput(type, message, params))
  }
  processComplete () {
    this.responseSubscription && this.responseSubscription.unsubscribe()
    this.responseSubscription = null
  }
  customGlobalObjects () {
    return {
      moment,
      expect,
      should,
      assert,
      BigNumber,
      fetch: axios,
      $test: this.testEngine,
      $result: this.testEngine.cases,
      clearInterval: (item) => {
        clearInterval(item)
      },
      setInterval: (func, time) => {
        setInterval(func, time)
      },
      clearTimeout: (item) => {
        clearTimeout(item)
      },
      setTimeout: (func, time) => {
        setTimeout(func, time)
      },
      $complete: () => {
        this.responseObserver && this.responseObserver.complete()
      },
      console: {
        error: (message, ...options) => {
          console.log('%c---------COMPILER-ERROR---------', 'background-image: linear-gradient(-90deg, #ff9a9e, #fad0c4); color: #FFFFFF')
          console.log(message, ...options)
          console.log('%c--------------------------------', 'background-image: linear-gradient(-90deg, #ff9a9e, #fad0c4); color: #FFFFFF')
        },
        info: (message, ...options) => {
          console.log('%c---------COMPILER-INFO---------', ' background-image: linear-gradient(-90deg, #a1c4fd, #c2e9fb); color: #FFFFFF ')
          console.log(message, ...options)
          console.log('%c-------------------------------', ' background-image: linear-gradient(-90deg, #a1c4fd, #c2e9fb); color: #FFFFFF ')
        },
        image: (url) => {
          const img = new Image() // eslint-disable-line
          img.onload = function () {
            const width = this.width
            const height = this.height
            console.log('%c+', ` font-size: 1px; padding-left: ${width * 2}px; width: ${width}px; height: ${height}px; line-height: ${height}px; background-image: url(${url}); background-repeat: no-repeat;`)
          }
          img.src = url
        },
        show: (url, {
          blackWhite = false
        }) => {
          const id = `image_${new Date().getTime()}`
          this.sendOutput(TYPES.image, url, {
            id: `${id}`,
            blackWhite
          })
          if (blackWhite) {
            setTimeout(() => {
              try {
                const canvas = document.getElementById(id)
                const context = canvas.getContext('2d')
                const image = new Image() // eslint-disable-line
                image.crossOrigin = 'Anonymous'
                image.src = url
                image.onload = (event) => {
                  try {
                    const width = image.width
                    const height = image.height
                    canvas.width = width
                    canvas.height = height
                    context.drawImage(image, 0, 0)
                    const imageData = context.getImageData(0, 0, width, height)
                    // Sobel constructor returns an Uint8ClampedArray with sobel data
                    const sobelData = Sobel(imageData)
                    // [sobelData].toImageData() returns a new ImageData object
                    const sobelImageData = sobelData.toImageData()
                    context.putImageData(sobelImageData, 0, 0)
                  } catch (err) {
                    this.sendOutput(TYPES.execuse_error, err.message)
                  }
                }
                image.onerror = () => {
                  this.sendOutput(TYPES.execuse_error, "image has been blocked by CORS policy: No 'Access-Control-Allow-Origin' header is present on the requested resource.")
                }
              } catch (err) {
                this.sendOutput(TYPES.execuse_error, err.message)
              }
            }, 500)
          }
        },
        log: (message) => {
          let type = TYPES.text
          if (typeof message === 'function') {
            type = TYPES.function
          } else if (typeof message !== 'string') {
            type = TYPES.other
          }
          this.sendOutput(type, message)
        },
        table: (message) => {
          this.sendOutput(TYPES.table, message)
        }
      },
      it: (name, callback) => this.testEngine.it(name, callback),
      test: (name, callback) => this.testEngine.it(name, callback),
      describe: (name, callback) => {
        return this.testEngine.describe(
          name,
          callback,
          (message) => this.sendOutput(TYPES.testcase, message)
        )
      },
      suite: (name, callback) => {
        return this.testEngine.describe(
          name,
          callback,
          (message) => this.sendOutput(TYPES.testcase, message)
        )
      }
    }
  }
}
