import React from 'react'
import {
  Divider,
  Collapse,
  TreeSelect
} from 'antd'
import { DEFAULT_HELP_TREE, DEFAULT_HELP_DATA } from '../model'
import ReactJson from 'react-json-view'

const ReactMarkdown = require('react-markdown/with-html')

class CodeResult extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      helpType: null,
      helpData: {
        key: 'basic',
        header: 'Documents',
        data: DEFAULT_HELP_DATA['basic']
      }
    }
    this.onChangeHelpType = this.onChangeHelpType.bind(this)
  }
  onChangeHelpType (helpType, label) {
    const { runtime } = this.props
    if (helpType === 'api' && runtime) {
      let data = `
  ## Accounts:

<div style='overflow-x: scroll'>

| Index | Name | Name | Address |
| ------- | ------- | ------- | ------- |
`
      runtime.accounts && runtime.accounts.forEach((item, index) => {
        data += `| ${index} | ${item.name} | ${item.key} | ${item.address} |\n`
      })
      data += `
</div>

## Contracts:
`
      runtime.contracts && runtime.contracts.forEach((item) => {
        data += `
---

  **${item.name}**

  Variable: ${item.key}

  Address: ${item.address}

  Functions:

`
        let abi = []
        try {
          abi = JSON.parse(item.abi)
          data += `

<div style='overflow-x: scroll'>

| Name | Input types | Input name | Output type | Output name |
| ------- | ------- | ------- | ------- | ------- |
`
          abi.forEach((functionItem) => {
            functionItem.inputs = functionItem.inputs || []
            functionItem.outputs = functionItem.outputs || []
            data += `| ${functionItem.name || ''} | ${functionItem.inputs.map(i => i.type)} | ${functionItem.inputs.map(i => i.name)} | ${functionItem.outputs.map(i => i.type)} | ${functionItem.outputs.map(i => i.name)} |\n`
          })
          data += `
</div>
`
        } catch (err) {
        }
      })
      return this.setState({
        helpType,
        helpData: {
          key: helpType,
          header: label,
          data
        }
      })
    }
    this.setState({
      helpType,
      helpData: {
        key: helpType,
        header: label,
        data: DEFAULT_HELP_DATA[helpType]
      }
    })
  }
  renderHelpData (data) {
    if (!data || !data.key || !data.data) {
      return null
    }
    return [
      <Divider key='header'>{data.header || ''}</Divider>,
      <ReactMarkdown
        key='content'
        skipHtml={false}
        rawSourcePos={false}
        sourcePos={false}
        escapeHtml={false}
        source={`
${data.data}
`}
      />
    ]
  }

  renderResults (outputs) {
    let components = []
    outputs && outputs.forEach((item, index) => {
      if (item.type === 'image' && !item.blackWhite) {
        components.push(<img
          key={item.id || index}
          id={item.id}
          alt={item.message}
          src={item.message}
          style={{
            maxWidth: '100%',
            height: 'auto'
          }}
        />)
      } else if (item.type === 'image' && item.blackWhite) {
        components.push(<canvas
          key={item.id || index}
          id={item.id}
          alt={item.message}
          style={{
            maxWidth: '100%',
            height: 'auto'
          }}
        />)
      } else if (typeof item.message === 'object') {
        components.push(<ReactJson
          key={index}
          indentWidth={2}
          collapsed
          iconStyle='square'
          src={item.message} />)
      } else {
        components.push(<ReactMarkdown
          key={index}
          skipHtml={false}
          rawSourcePos={false}
          sourcePos={false}
          escapeHtml={false}
          source={`
${item.message}\n
`}
        />)
      }
    })
    return components
  }
  render () {
    const { helpData, helpType } = this.state
    const { outputs } = this.props
    const components = this.renderResults(outputs)
    return (
      <div style={{ flex: 1, overflowY: 'auto', height: '100%', paddingLeft: 10 }}>
        <Collapse
          accordion
          defaultActiveKey={['result']}
        >
          <Collapse.Panel
            header='RESULT'
            key='result'
          >
            <div
              style={{
                width: '100%',
                height: '100%',
                minHeight: '250px',
                overflowX: 'auto'
              }}
            >
              {components.map(item => item)}
            </div>
          </Collapse.Panel>
          <Collapse.Panel header='DOCUMENTS' key='helper'>
            <TreeSelect
              style={{ width: '100%' }}
              value={helpType}
              dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
              treeData={DEFAULT_HELP_TREE}
              placeholder='What would you like to know ?'
              treeDefaultExpandAll
              onChange={this.onChangeHelpType}
            />
            {this.renderHelpData(helpData)}
          </Collapse.Panel>
        </Collapse>
      </div>
    )
  }
}

export default CodeResult
