import React from 'react'
import I18n from 'i18n-js'
import 'brace'
import 'brace/mode/javascript'
import 'brace/theme/monokai'
import AceEditor from 'react-ace'
import {
  Icon,
  Menu,
  Button,
  message,
  Dropdown
} from 'antd'

class CodeEditor extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      ready: false,
      code: `// SELECT RUNTIME FOR START CODING
// TODO: Your code here
`
    }
    this.factory = null
    this.compiler = null
    this.fileInput = null
    this.saveFile = this.saveFile.bind(this)
    this.onCompile = this.onCompile.bind(this)
    this.selectFile = this.selectFile.bind(this)
    this.selectMenu = this.selectMenu.bind(this)
    this.onChangeCode = this.onChangeCode.bind(this)

    this.menu = (
      <Menu onClick={this.selectMenu} style={{ width: 120 }}>
        <Menu.Item key='open'>
          <Icon type='folder-open' /> Open
        </Menu.Item>
        <Menu.Item key='save'>
          <input
            ref={(ref) => {
              this.fileInput = ref
            }}
            type='file'
            onChange={this.selectFile}
            accept='application/javascript'
            style={{ width: 0, height: 0, overflow: 'hidden' }}
          />
          <Icon type='save' style={{ marginRight: 10 }} /> Save
        </Menu.Item>
      </Menu>
    )
  }
  selectFile (e) {
    try {
      const files = e.target.files
      const reader = new FileReader() // eslint-disable-line
      reader.onload = (upload) => {
        try {
          this.setState({
            code: upload.target.result
          })
        } catch (err) {
          message.error(I18n.t('errors.read_contract_file_error'))
        }
      }

      reader.readAsText(files[0], 'TF-8')
    } catch (err) {
    }
  }
  saveFile () {
    const { code } = this.state
    const { saveSourceFile } = this.props
    saveSourceFile(code)
  }
  selectMenu (menu) {
    switch (menu.key) {
      case 'open':
        return this.fileInput && this.fileInput.click()
      case 'save':
        return this.saveFile()
      default:
        break
    }
  }
  componentDidMount () {
    const { createSanboxCompiler } = this.props
    this.compiler = createSanboxCompiler()
    this.setState({
      ready: true
    })
  }
  onCompile () {
    const { code } = this.state
    const { compileSource } = this.props
    compileSource(code, this.compiler)
  }
  onChangeCode (code) {
    this.setState({
      code
    })
  }
  render () {
    const { ready, code } = this.state
    return (
      <div style={{
        height: '100%',
        width: '100%',
        display: 'flex',
        flexDirection: 'column'
      }}>
        <div
          style={{
            flex: 1,
            width: '100%'
          }}
        >
          <AceEditor
            value={code}
            mode='javascript'
            theme='monokai'
            name='CODE_EDITOR'
            onChange={this.onChangeCode}
            editorProps={{
              $blockScrolling: 'Infinity'
            }}
            setOptions={{
              enableLiveAutocompletion: true,
              enableBasicAutocompletion: true,
              enableSnippets: true,
              showLineNumbers: true,
              tabSize: 2
            }}
            style={{
              width: '100%',
              height: 460
            }}
          />
        </div>
        <div style={{ height: 40, marginTop: 10, display: 'flex', flexDirection: 'row' }}>
          <Dropdown overlay={this.menu} placement='bottomCenter'>
            <Button type='primary' shape='circle-outline'>
              <Icon type='file' width='3em' height='3em' style={{ paddingBottom: 10 }} />
            </Button>
          </Dropdown>
          <Button
            style={{ marginLeft: 5, flex: 1 }}
            type='primary'
            disabled={!ready}
            onClick={this.onCompile}
          >
            Run
          </Button>
        </div>
      </div>
    )
  }
}

export default CodeEditor
